<template>
  <div class="page">
    <FormSelectGroup v-on:submit-query="submitshow" :module="'reportpublic'" /> 
    <CommonSuspend class="content-area" :suspendobj="suspendOpt" v-on:select-dialog="selectDialog">
      <div class="underlinebtn">
        <SwitchChange :switchOpt="switchOpt" v-on:switch-change="switchChange"/>
        <el-button type="primary" size="small" @click="publics" v-show="publicflag">发布</el-button>
      </div>
      <Scrollframe class="content-frame" :isLastPage="isLastPage"  v-on:ajax-query="ajaxshow" >
          <div class="receivelist" @mouseover="overreportlist(item)" @mousedown="popclick(item)" v-for="(item,index) in items" :key="index">
              <div class="list-top">
                  <span v-if="publicflag" :title="item.reportTitle">{{item.reportTitle}}</span>
                  <div v-if="!publicflag" class="titlelist">
                    <div><span :title="item.reportTitle">{{item.reportTitle}}</span></div>
                    <div><span>接收人：</span><span :title="item.reportTitle">{{item.receiveUserName}}</span></div>
                    <div><span>发布时间：</span><span :title="item.reportTitle">{{item.publishTime}}</span></div>
                  </div>
                  <!-- <span  class="multipletitle" v-if="!angleshowflag" v-for="(list,ind) in item.titleList" :key="ind">
                    {{list.reportTitle}}
                  </span> -->
                  <div class="angle" v-if="angleshowflag" v-show="!item.publishStatus">
                    <div class="obliqueangle">未发布</div>
                  </div>
              </div>  
              <div class="combination" v-if="checkboxshow"><el-checkbox v-model="item.checked" @change="changecombination(item)"></el-checkbox></div>      
          </div>
      </Scrollframe>
    </CommonSuspend>
     <Dialog  :dialogOpt="dialogOpt" v-on:handlecancle-dialog="selectDialog" v-on:handlesure-dialog="submitDialog"></Dialog> 
  </div>
</template>

<script>
import FormSelectGroup from "@/components/formSelectGroup.vue"; //select选择组
import CommonSuspend from "@/components/commonSuspend.vue"; //通用悬浮
import Scrollframe from "@/components/Scrollframe.vue"; //滚动下拉加载
import SwitchChange from "@/components/switchChange.vue"; //模拟滑动开关
import Dialog from "./dialog.vue"; //本页所有弹窗内容集合(每个页面单独添加)
import server from "./server.js"; //本页所有接口内容集合(每个页面单独添加)
export default {
  data() {
    return {
      //必须配置项
      isLastPage: false, //最后一页
      pagepation: {
        //翻页配置
        pageNum: 1,
        pageSize: 15
      },
      serverobject: server, //ajax服务
      items: [], //列表内容数据

      angleshowflag: true, //未发布左上角斜标
      // value1: true,
      // value2: true,
      // items: [],
      dialogOpt:{
        "dialogflag": false,
        "width":"536px",
      },
      suspendOpt:{
        name:"报告查看",  //唯一区别遮罩的html内容（报告发布的全部展示和报告查看一样）
        position:"bottom",  //遮罩部件在位置
        domClass:"receivelist",  //遮罩部件
        offsetbottom:"10%",    //遮罩层距离底部的距离
        show:true,       //遮罩层是否显示控件
        bgshow:false,
      },
      switchOpt: {
        //滑动开关组件
        width: "150px", //总宽度
        leftName: "全部", //左边开关的内容
        rightName: "已发布" //右边开关的内容
      },
      publicflag:true,    //是否显示发布按钮
      reports:[],   //勾选的报告对象集合
      reportsindex:{},    //点击的报告对象
      checkednumber:0,
      reportIdlists:[],   //合并报表的id
      reportshowtype:[],   //展示报表类型
      checkboxshow:false,
    };
  },
  components: {
    FormSelectGroup,
    Scrollframe,
    CommonSuspend,
    Dialog,
    SwitchChange
  },
  methods: {
    overreportlist(item){   //在遮罩显示之前确定编辑是否显示
      // 0不是|1是合并
      this.$set(this.suspendOpt,"editshowflag",item.isMerger)
    },
    popclick(item){   //点击的报告对象
      this.reportsindex = item;
    },    
    switchChange(message) {
      //开关切换组件
       let self = this;
       self.items = [];
       self.pagepation={
          pageNum: 1,
          pageSize: 15
       }
      if (message == "全部") {
        self.publicflag=true;
        self.angleshowflag = true;
        self.$set(self.suspendOpt,"name","报告查看")

        self.isLastPage=true;   //在请求数据期间避免重复加载
        self.serverobject.pagepation = self.pagepation; //翻页配置
        server.reportlist("",function success(res) {  //接口请求
          let data = res.data.data;
          self.items = self.items.concat(data.list);
          self.isLastPage = data.isLastPage;    //是否最后一页
        });        
      } else {
        //已发布
        self.checkboxshow = false;
        self.publicflag=false;
        self.angleshowflag = false;
        self.$set(self.suspendOpt,"name","报告发布")

        self.isLastPage=true;   //在请求数据期间避免重复加载
        self.serverobject.pagepation = self.pagepation; //翻页配置
        server.reportlistpublish("",function success(res) {  //接口请求
          let data = res.data.data;
          self.items = self.items.concat(data.list);
          self.isLastPage = data.isLastPage;    //是否最后一页
        });
      }
    },
    publics(){      //发布
      this.checkboxshow = true;
      this.publicsflag = true;
      if(this.checkednumber>0){
        this.selectDialog("发布")    //先保存然后在发布
      }
    }, 
    changecombination(item){   //复选框发生change事件。
      this.reportIdlists.push(item.reportId)    //报告id用于合并
      this.reportshowtype.push(item.showType)    //报告类型用于判断是否可以合并
      if(item.checked){
        this.checkednumber++
        this.reports.push(item)
      }else{
        this.checkednumber--
        this.reports.forEach((obj,index)=>{
          if(obj.reportId == item.reportId){
            this.reports.splice(index,1)
          }
        })
      }
    },    
    selectDialog:function(message,type){   //弹窗打开/关闭（message对话框标题，type是操作类型sure为保存close为取消）
      if(type){   //关闭弹窗
        this.dialogOpt.dialogflag=false;
        self.publicsagainflag=false;
      }else{    //打开弹窗
            this.dialogOpt= {
              "dialogflag":true,     //根据flag决定弹窗是否出现
              "width":"536px",
              "title":message       //根据title决定弹窗页面内容。
            }
      }
    },    
    ajaxshow: function(message) {   //ajax请求数据
      let self = this;
      self.isLastPage=true;   //在请求数据期间避免重复加载
      self.pagepation = message?message:self.pagepation;    //翻页
      self.serverobject.pagepation = self.pagepation; //翻页配置
      server.reportlist(message,function success(res) {  //接口请求
        let data = res.data.data;
        self.items = self.items.concat(data.list);
        self.isLastPage = data.isLastPage;    //是否最后一页
        console.dir(self.items)
      });
    },
    submitshow: function(message) {
      //列表查询条件提交
      this.pagepation = {   //翻页重置
        pageNum: 1,
        pageSize: 15
      },
      this.items=[];
      let messages = Object.assign(message, this.pagepation);      
      this.ajaxshow(messages)
    },
    submitDialog:function(message,type,formdata){    //对话框保存(formdata为对话框表单对象)
      let self=this;
      let formdatas=formdata;
      if(message == "发布"){
          let nweparam = [];
          self.reports.forEach(obj=>{
            formdatas["receiveUserId"].forEach(objchild=>{
              nweparam.push({
                  reportId:obj.reportId,
                  allowTwice:formdatas.allowTwice,
                  urgentType:formdatas.urgentType,
                  usefulTime:formdatas.usefulTime,
                  remark:formdatas.remark,
                  reportTitle:obj.reportTitle,
                  receiveUserId:objchild.id,
                  receiveUserName:objchild.label
                })
            })
          })
          server.reportpublish(nweparam,function success(res) {  //接口请求、
            if(res.data.status = "SUCCESS"){
                self.items=[]
                self.checkboxshow = false;
                self.dialogOpt.dialogflag=false;    //弹窗取消
                self.publicsagainflag=false;
                self.ajaxshow();
            }
          });  
      }
      if(message == "修改主题分类"){
          let params = Object.assign({reportId:self.reportsindex.reportId},formdata);
          server.reportthemeupdate(params,function success(res) {  //接口请求、
            if(res.data.status = "SUCCESS"){
                self.dialogOpt.dialogflag=false;
                self.items=[];
                self.ajaxshow()
            }
          });
      }
      if(message == "删除"){
        if(self.publicflag){    //全部
          let params = self.reportsindex.reportId;
          server.reportsdelete(params,function success(res) {  //接口请求、
            if(res.data.status = "SUCCESS"){
                self.dialogOpt.dialogflag=false;
                self.items=[];
                // self.ajaxshow()
                self.switchChange("全部")
            }
          });
        }else{    //已发布
          let params = self.reportsindex.publishId;
            server.reportspublishdelete(params,function success(res) {  //接口请求、
              if(res.data.status = "SUCCESS"){
                  self.dialogOpt.dialogflag=false;
                  self.items=[];
                  // self.ajaxshow()
                  self.switchChange("已发布")
              }
          });

        }
      }      
    },     
  }
};
</script>

<style scoped lang="less">
.underlinebtn {
  justify-content: space-between;
}
.content-frame {
  border-radius: 10px;
  background-color: #ffffff;
  width: 100%;
  min-height: 300px;
  margin-top: -40px;
  padding: 40px 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .receivelist {
    min-width: 320px;
    min-height: 215px;
    width: 3.05rem;
    height: calc(3.05rem - 100px);
    margin: 17px 0px;
    background-color: #f4f4f4;
    box-shadow: 0px 0px 7px 3px rgba(0, 0, 0, 0.09);
    float: left;
    position: relative;
    overflow: visible !important;
    .list-top {
      width: 100%;
      height: 100%;
      text-align: center;
      color: #ffffff;
      font-size: 2em;
      background: linear-gradient(#6886db, #4b63a7);
      padding-top: 50px;
      .titlelist{
         display: flex;
         flex-direction: column;
      }
      .angle {
        position: absolute;
        top: 0;
        left: 0;
        width: 150px;
        height: 150px;
        overflow: hidden;
        .obliqueangle {
          position: absolute;
          top: 0;
          left: 0;
          width: 150px;
          height: 30px;
          background: #a3afb7;
          font-size: 20px;
          transform: translate(-25%, 77%) rotate(-45deg);
        }
      }
      .multipletitle {
        display: inline-block;
        width: 100%;
        line-height: 30px;
      }
    }
    .combination{
      text-align: center;
      line-height: 30px;
      .el-checkbox__inner{
          width: 16px;
          height: 16px;
          border:1px solid;
      }
    }
  }
}
</style>



